import React from 'react';

import { Docs, DocsExample, DocProps, DocsSubtitle } from '@doc-utils';
import propsSrc from './generated-props.json';
import examples from './generated-examples.json';

import { SegmentedButton, Segment } from '.';
import { TouchTargetWrapper } from '@rmwc/touch-target';

export default function Readme() {
  return (
    <Docs
      title="Segmented Button"
      lead="Segmented buttons allow users to toggle the selected states of grouped buttons."
      module="@rmwc/segmented-button"
      styles={[
        '@material/segmented-button/dist/mdc.segmented-button.css',
        '@rmwc/@rmwc/icon/icon.css',
        '@material/ripple/dist/mdc.ripple.css'
      ]}
      docsLink="https://material.io/develop/web/components/segmented-button/"
      examples={examples}
    >
      <DocsExample label="Default">
        <SegmentedButton>
          <Segment icon="favorite" />
          <Segment label="Button" />
          <Segment icon="favorite" label="Button" selected />
        </SegmentedButton>
      </DocsExample>

      <DocsExample label="Multiple selectable">
        <SegmentedButton selectType="multiple">
          <Segment icon="favorite" />
          <Segment label="Button" />
          <Segment icon="favorite" label="Button" />
        </SegmentedButton>
      </DocsExample>

      <DocsExample label="Single selectable">
        <SegmentedButton selectType="single">
          <Segment icon="favorite" selected />
          <Segment label="Button" />
          <Segment icon="favorite" label="Button" />
        </SegmentedButton>
      </DocsExample>

      <DocsExample label="Touch Target Wrapper">
        <>
          {/** Wrapping a button in TouchTargetWrapper will automatically set its `touch` prop to true. */}
          <TouchTargetWrapper>
            <Segment>Touch Accessible</Segment>
          </TouchTargetWrapper>
        </>
      </DocsExample>

      <DocsSubtitle>Controlled/Uncontrolled</DocsSubtitle>

      <DocsExample label="Uncontrolled">
        <SegmentedButton>
          <Segment icon="favorite" />
          <Segment label="Button" />
          <Segment icon="favorite" label="Button" />
        </SegmentedButton>
      </DocsExample>

      <DocsExample label="Controlled">
        {function Controlled() {
          const [selected, setSelected] = React.useState(-1);
          return (
            <SegmentedButton
              onActivate={(evt) => {
                console.log(evt.detail.index);
                setSelected(evt.detail.index);
              }}
            >
              <Segment icon="favorite" selected={selected === 0} />
              <Segment label="Button" selected={selected === 1} />
              <Segment
                icon="favorite"
                label="Button"
                selected={selected === 2}
              />
            </SegmentedButton>
          );
        }}
      </DocsExample>

      <DocProps
        src={propsSrc}
        components={[
          { displayName: 'SegmentedButton', component: SegmentedButton }
        ]}
      />
    </Docs>
  );
}

export const galleryExample = <SegmentedButton label="Cookie" />;
